body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.overlayContainer{
  pointer-events: none;
  width: 100%;
  text-align: center;
  padding: auto;
  position: absolute;
  top: 0;
  margin-top: 1%;
}

#mouseCoordinates{
  text-align: center;
  padding: 0.7%;
  margin: auto;
  background-color: #FFF;
  color: #4a4a4a;
  border: 0.5px solid #b2b2b2;
  border-radius: 6px;
  width: 40%;
}

.SearchBar{
  width: 100%;
  position: absolute;
  top: 0;
  margin-top: 4%;
  margin-left: 0.5%;
}

#searchbarInput{
  width: 15%;
  padding: 1%;
  border-radius: 5px;
  border: 0.5px solid #c7c7c7;
  font-size: 15px;
}

.suggestionsbox{
  background-color: #fff;
  width: 15%;
  padding: 1%;
}
